/* header css */
@charset "utf-8";

@font-face {
    font-family: 'roboto-mono';
    src: url(/public/fonts/Roboto_Mono/static/RobotoMono-Regular.ttf);
}

* {
}

/* 헤더 */
.header-wrap {
    position: fixed;
    width: calc(100% - 50px);
    height: 60px;
    top: 16px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 12px;
    background: #fff;
    /* box-shadow: 0 4px 10px -4px rgba(0, 0, 0, .45); */
    z-index: 400;
}

header {
    position: relative;
    width: 100%;
    height: 100%;
}
