/* 로고 */
.logo-wrap {
    position: absolute;
    left: 20px;
    width: 120px;
    height: 100%;
}

.logo-wrap h1 {
    width: 100%;
    height: 100%;
}

.logo {
    display: block;
    background-image: url('/public/images/logo.gif');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
}

.gnb {
    width: 100%;
    height: 100%;
    text-align: center;
}

.gnb .list-wrap {
    display: inline-block;
    height: 100%;
}

.gnb-ul {
    display: flex;
    width: 100%;
    height: 100%;
}

.gnb-li {
    display: inline-block;
    width: 100px;
    height: 100%;
}

.gnb-li-link {
    display: block;
    width: 100%;
    height: 100%;
    line-height: 60px;
    font-family: 'roboto-mono';
    color: #555;

}


/* hamburger btn */
.gnbBtn {
    position: fixed;
    top: 12px;
    right: 12px;
    width: 45px;
    height: 45px;
    z-index: 900;

}

.btnBox {
    position: fixed;
    background-color: #fff;
    top: 14px;
    right: 14px;
    width: 37px;
    height: 37px;
    border-radius: 10px;
    transform: rotate(45deg);
    border: 1.4px solid #555;
}



.gnbBtn span {
    position: absolute;
    width: 24px;
    height: 1.22px;
    background: #555;
    border-radius: 8px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: all .3s ease;
}

.gnbBtn span:first-child {
    top: 14px;
}

.gnbBtn span:last-child {
    top: 30px;
}

.gnbBtn.active span:first-child {
    top: 22px;
    transform: translate(-50%, -50%) rotate(45deg);
}

.gnbBtn.active span:nth-child(2) {
    display: none;
}

.gnbBtn.active span:last-child {
    top: 22px;
    transform: translate(-50%, -50%) rotate(-45deg);
}


/* mobile gnb css */
.mo .logo-wrap {
    position: relative;
}

.mo .gnb {
    position: absolute;
    right: -100%;
    opacity: 0;
    width: 100%;
    height: 100vh;
    transition: all .3s ease;
}

.mo .gnb.active {
    right: 0;
    opacity: 1;

}



